
import * as types from './mutation-types'

import {saveSearch, clearSearch, deleteSearch, saveUser, changeUser, clearUser, saveUserAddress, changeUserAddress, saveUserCity} from 'common/js/cache'
/* 搜索历史 */
export const saveSearchHistory = function ({commit}, query) {
  commit(types.SET_SEARCH_HISTORY, saveSearch(query))
}
/* 用户相关信息 */
export const saveUserAccount = function ({commit}, user) {
  commit(types.SET_USERACCOUNT, saveUser(user))
}

export const changeUserAccount = function ({commit},user) {
  commit(types.SET_USERACCOUNT, changeUser(user))
}

export const clearUserAccount = function ({commit}) {
  commit(types.SET_USERACCOUNT, clearUser())
}

/* 地址 */
export const saveAddress = function ({commit}, address) {
  commit(types.SET_ADDRESS, saveUserAddress(address))
}

export const changeAddress = function ({commit},address) {
  commit(types.SET_ADDRESS, changeUserAddress(address))
}

/*地区 */

export const saveCity = function ({commit}, city) {
  commit(types.SET_CITY, saveUserCity([city]))
}


