/* 添加购物车接口*/

export function addGoodToCard(uid,token,group_id,num) {
  const url = api.base + '/api.php?g=api&c=Usercard&a=addgoodtocard'

  const data = {
    uid:uid,
    token:token,
    group_id:group_id,
    num: num
  }
  return axios.post(url, obj2params(paramsPrefilter(data)),{
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded'
    }
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

