import storage from 'good-storage'

const SEARCH_KEY = '__search__'
const SEARCH_MAX_LEN = 15

const USERACCOUNT_KEY = '__useraccount__'

const ADDRESS_KEY = '__address__'

const CITY = '__city__'

function insertArray(arr, val, compare, maxLen) {
  const index = arr.findIndex(compare)
  if (index === 0) {
    return
  }
  if (index > 0) {
    arr.splice(index, 1)
  }
  arr.unshift(val)
  if (maxLen && arr.length > maxLen) {
    arr.pop()
  }
}

function deleteFromArray(arr, compare) {
  const index = arr.findIndex(compare)
  if (index > -1) {
    arr.splice(index, 1)
  }
}

export function saveSearch(query) {
  let searches = storage.get(SEARCH_KEY, [])
  insertArray(searches, query, (item) => {
    return item === query
  }, SEARCH_MAX_LEN)
  storage.set(SEARCH_KEY, searches)
  return searches
}

export function deleteSearch(query) {
  let searches = storage.get(SEARCH_KEY, [])
  deleteFromArray(searches, (item) => {
    return item === query
  })
  storage.set(SEARCH_KEY, searches)
  return searches
}

export function clearSearch() {
  storage.remove(SEARCH_KEY)
  return []
}

export function loadSearch() {
  return storage.get(SEARCH_KEY, [])
}

export function saveUser(user) {
  // 存取用户的userAccount相关信息
  let userAccount = storage.set(USERACCOUNT_KEY, user)
  return userAccount
}

export function loadUser() {
  return storage.get(USERACCOUNT_KEY, [])
}

export function changeUser(newUser) {
  let userAccount = storage.set(USERACCOUNT_KEY, newUser)
  return userAccount
}

export function clearUser() {
  storage.remove(USERACCOUNT_KEY)
  return []
}

export function saveUserAddress(address) {
  let userAddress = storage.set(ADDRESS_KEY,address)
  return userAddress
}

export function changeUserAddress(address) {
  let userAccount = storage.set(ADDRESS_KEY, address)
  return userAccount
}

export function loadUserAddress() {
  return storage.get(ADDRESS_KEY,[])
}

// 选择城市

export function saveUserCity(city) {
  let userCity = storage.set(CITY,city)
  console.log(userCity)
  return userCity
}
export function loadCity() {
  return storage.get(CITY, ['青岛'])
}



