import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

// 这里采用按需加载的模式

const Index = (resolve) => {
  import('components/index/index').then((module) => {
    resolve(module)
  })
}

const Category = (resolve) => {
  import('components/category/category').then((module) => {
    resolve(module)
  })
}

const CategoryDetail = (resolve) => {
  import('components/category/category-detail').then((module) => {
    resolve(module)
  })
}


const Detail = (resolve) => {
  import('components/detail/detail').then((module) => {
    resolve(module)
  })
}
const Shopcart = (resolve) => {
  import('components/shopcart/shopcart').then((module) => {
    resolve(module)
  })
}
const Order = (resolve) => {
  import('components/order/order').then((module) => {
    resolve(module)
  })
}
const Redpacket = (resolve) => {
  import('components/red-packet/red-packet').then((module) => {
    resolve(module)
  })
}

const Me = (resolve) => {
  import('components/me/me').then((module) => {
    resolve(module)
  })
}
const City = (resolve) => {
  import('components/city/city').then((module) => {
    resolve(module)
  })
}
const Menulist = (resolve) => {
  import('components/menulist/menulist').then((module) => {
    resolve(module)
  })
}
const Search = (resolve) => {
  import('components/search/search').then((module) => {
    resolve(module)
  })
}
const Login = (resolve) => {
  import('components/login/login').then((module) => {
    resolve(module)
  })
}
const Set = (resolve) => {
  import('components/set/set').then((module) => {
    resolve(module)
  })
}
const Address = (resolve) => {
  import('components/address/address').then((module) => {
    resolve(module)
  })
}
const Newaddress = (resolve) => {
  import('components/address/newaddress').then((module) => {
    resolve(module)
  })
}
const Register = (resolve) => {
  import('components/register/register').then((module) => {
    resolve(module)
  })
}

const AllOrder = (resolve) => {
  import('components/myorder/my-order').then((module) => {
    resolve(module)
  })
}
export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/index',
      meta: { keepAlive: true }
    },
    {
      path: '/index',
      component: Index,
      meta: { keepAlive: true }
    },
    {
      path: '/category',
      component: Category,
      meta: { keepAlive: true }
    },
    {
      path: '/category-detail',
      component: CategoryDetail,
      meta: { keepAlive: true }
    },
    {
      path: '/detail',
      component: Detail
    },
    {
      path: '/shopcart',
      component: Shopcart
    },
    {
      path: '/order',
      component: Order
    },
    {
      path: '/redpacket',
      component: Redpacket
    },
    {
      path: '/me',
      component: Me
    },
    {
      path: '/city',
      component: City
    },
    {
      path: '/menulist',
      component: Menulist
    },
    {
      path: '/search',
      component: Search
    },
    {
      path: '/login',
      component: Login
    },
    {
      path: '/set',
      component: Set
    },
    {
      path: '/address',
      component: Address
    },
    {
      path: '/newaddress',
      component: Newaddress
    },
    {
      path: '/register',
      component: Register
    },
    {
      path: '/myorder',
      component: AllOrder
    }
  ]
})



